.friendList {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  margin-top: 30px;
  margin-bottom: 30px;
  width: 300px;
  font-family: "Indie Flower", cursive;
}

.item {
  color: black;
  width: 300px;
  background-color: lightgrey;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: 10px 25px;
  box-shadow: rgb(0 0 0) 0px 2px 7px -2px;
  border: 2px solid lightgreen;
  border-radius: 5px;
}
.item:not(:last-child) {
  margin-bottom: 15px;
}
