.profile-wrapper {
  /* font-family: "Comic Neue", cursive; */
  letter-spacing: 0.03em;
  line-height: 1.1;
  width: 400px;
  height: auto;
  margin: 0 auto;
  margin-top: 30px;
  border: 1px solid #afb1b8;
  border-radius: 8px;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
}

.description {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color: lightskyblue;
  border-radius: 8px;
  padding: 30px;
}

.avatar {
  width: 80%;
  height: auto;
  border-radius: 50%;
}

.name {
  margin-top: 30px;
  font-size: 32px;
  color: indigo;
  font-weight: 900;
}

.tag {
  margin-top: 10px;
  color: darkslateblue;
  font-size: 20px;
  font-weight: normal;
}
.location {
  margin-top: 10px;
  color: darkslateblue;
  font-size: 24px;
  font-weight: normal;
}

.stats {
  display: flex;
  align-items: flex-start;
  background-color: lightgrey;
}

.item {
  padding: 10px 0;
  height: 80px;
  align-items: center;
  justify-content: space-between;
  display: flex;
  flex-direction: column;
  width: calc(100% / 3);
  border: 1px solid #afb1b8;
}

.label {
  font-size: 22px;
  color: indigo;
  font-weight: 400;
}

.quantity {
  font-size: 25px;
  color: indigo;
  font-weight: 900;
}
