.statistics {
  display: flex;
  flex-direction: column;
  -webkit-box-pack: justify;
  justify-content: space-between;
  margin-left: auto;
  margin-right: auto;
  margin-top: 50px;
  border-radius: 2px;
  background-color: rgb(236, 241, 244);
  box-shadow: rgb(2 2 2 / 2%) 0px 2px 2px 0px;
  width: 400px;
}
.title {
  margin: auto 0;
  font-size: 35px;
  font-weight: 900;
  color: darkslategrey;
}
.statList {
  display: flex;
  justify-content: flex-start;
  flex-direction: row;
  align-items: center;
  width: 100%;
  margin-top: 10px;
  padding: 0;
}

.item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 80px;
  padding: 15px 0;
  border: 1px solid grey;
  border-radius: 3px;
}

.label {
  margin-bottom: 10px;
  color: black;
  font-size: 18px;
}
.percentage {
  color: rgb(255, 255, 255);
  font-size: 24px;
}
